import React, { Component } from 'react'
import ReactSwipe from 'react-swipe'
import {Link} from 'react-router-dom'

import './style.scss'

class Category extends Component {
    constructor(props) {
        super(props)
        this.state = {
            index:0
        }
    }
    
    SwipeCategory(e){

		// this.setState({
		// 	index:parseInt(e.target.getAttribute("data-index"))
		// })

		e.preventDefault();
		e.stopPropagation();
	}

    render() {
        const opt = {
			auto: 5000,
			callback:(index)=>{
				this.setState({
					index: index
				})
			}
		}
        return (
            <div className="home-category">
                <ReactSwipe swipeOptions={opt}>
                    <div className="carousel-item">
                        <ul>
                            <li><Link to="/home"><i className="icon">&#xe65a;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe614;</i>KTV</Link></li>
                            <li><Link to="/home"><i className="icon">&#xe606;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe654;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe65d;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe649;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe62e;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe617;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe659;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe64b;</i></Link></li>
                        </ul>
                    </div>
                    <div className="carousel-item">
                        <ul>
                            <li><Link to="/home"><i className="icon">&#xe65c;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe6e3;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe678;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe605;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe703;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe60b;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe7c6;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe616;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe601;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe613;</i></Link></li>
                        </ul>
                    </div>
                    <div className="carousel-item">
                        <ul>
                            <li><Link to="/home"><i className="icon">&#xe602;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe6e5;</i>SPA</Link></li>
                            <li><Link to="/home"><i className="icon">&#xe6e4;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe604;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe61f;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe62f;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe61b;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe603;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe672;</i></Link></li>
                            <li><Link to="/home"><i className="icon">&#xe6f4;</i></Link></li>
                        </ul>
                    </div>
                </ReactSwipe>
                <div className="index-container">
                    <ul>
                        <li className={this.state.index === 0 ? "selected" : ''} data-index="0" onClick={this.SwipeCategory.bind(this)}></li>
                        <li className={this.state.index === 1 ? "selected" : ''} data-index="1" onClick={this.SwipeCategory.bind(this)}></li>
                        <li className={this.state.index === 2 ? "selected" : ''} data-index="2" onClick={this.SwipeCategory.bind(this)}></li>
                    </ul>
                </div>
            </div>
        );
    }

    
}

export default Category;


